@import "./../Global.scss";

.pText {
    font-size: large;
    width: 28vw;
    color: white;
}


.contactGrid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-template-rows: 1fr;
    grid-template-areas: "a b";
    flex-direction: column;
}

.contactFlex {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.contact-form {
    display: flex;
    flex-direction: column;
    align-items:flex-start;
    margin-top: 4%;
    background-color: $primary-color;
    padding: 3%;
    // margin-right: 12%;
}

input {
    width: 25vw;
    margin-bottom: 20%;
}

.last {
    margin-bottom: 0px;
}

.title2 {
    margin-bottom: 2%;
}